






































































import { Component, Vue } from "vue-property-decorator";
import DataService from "../services/DataService";

@Component
export default class Kicker extends Vue {
  private currentKicker: any = null;
  private password: string = "";
  private message: string = "";

  getKicker(id: string) {
    DataService.get(id)
      .then((response) => {
        this.currentKicker = response.data;
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }

  updateActive(status: boolean) {
    if(confirm("Sicher dass der Kicker auf " + (status ? "aktiv": "inaktiv") + " gesetzt werden soll?")) {
      DataService.active(this.currentKicker.id, status)
        .then((response) => {
          this.currentKicker.published = status;
          console.log(response.data);
          this.message = "Status erfolgreich geändert";
        })
        .catch((e) => {
          console.log(e);
        });
    }
  }

  changePassword() {
    if(confirm("Sicher dass das Passwort geändert werden soll?")) {
      DataService.changePassword(this.currentKicker.id, this.password)
        .then((response) => {
          console.log(response.data);
          this.message = "Passwort erfolgreich geändert";
        })
        .catch((e) => {
          console.log(e);
        });
    }
  }

  updateKicker() {
    if(confirm("Sicher dass der Name oder die E-Mail des Kickers geändert werden soll?")) {
      let data = {
        name: this.currentKicker.name,
        email: this.currentKicker.email,
      };
      DataService.update(this.currentKicker.id, data)
        .then((response) => {
          console.log(response.data);
          this.message = "Daten erfolgreich gespeichert";
        })
        .catch((e) => {
          console.log(e);
        });
    }
  }

  deleteKicker() {
    if(prompt("Sicher dass der Spieler \"" + this.currentKicker.name + "\" gelöscht werden soll? Dann gib bitte den Namen zur Bestätigung ein:") === this.currentKicker.name) {
      DataService.delete(this.currentKicker.id)
        .then((response) => {
          console.log(response.data);
          this.$router.push({ name: "kicker" });
        })
        .catch((e) => {
          console.log(e);
        });
    }
  }

  mounted() {
    this.message = "";
    this.getKicker(this.$route.params.id);
  }
}
